.profile{
    grid-area: content;
}

.preloader {
    opacity: 30%;
}
/* .profileInfo img{
    border-radius: 50%;
} */

.contactData {
    padding-left: 15px;
}

.appearance {
    display: flex;
}
.photoblock {
    position: relative;
    margin-right: 10px;
}
.photoblock img {
    border-radius: 10px;;
}


.upload {
    display: none;
    position: absolute;
    top: 10px;
    left: 145px;
}

.upload input {
    /* visibility: hidden; */
    display: none;
}
.upload label {
    margin: 1px;
    border-radius: 4px;
    background: linear-gradient(#c8e4eb, #a7b5b7,#c8e4eb);
    padding: 3px 7px;
    color: #333;
    border-radius: 5px;
    border: 1px solid rgb(129, 129, 129);
}
/* "background-color: #000; color:#fff; padding:4px; border-radius:4px; " */

.photoblock:hover .upload {
    display: block;
} 

         /* status */

.statusblock {
    margin-bottom: 100px;
}


        /* ProfileData */
    
.profileData {
    position: relative;
}
.profileData .manageData {
position: absolute;
top: 0;
right: 100%;
left: 100%;
width: 100px;
}